@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Raleway:wght@300;500;600&display=swap);
.modal {
    position: absolute;
    top: 0;
    left: 0;
    background: var(--color-dark);
    z-index: 100;
    min-height: 100vh;
    width: 100%;
    padding: 17px 13px;
}

.modal.hide {
    display: none;
}

@media (min-width: 768px) {
    .modal {
        width: 40%;
    }
}

@media (min-width: 1200px) {
    .modal {
        width: 30%;
    }
}

.modal .close {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 30px;
}

.modal .close > button {
    background: transparent;
    color: var(--color-light);
}

.modal .form_search {
    display: grid;
    grid-template-columns: calc(80% - 13px) 20%;
    grid-gap: 12px;
    gap: 12px;
}

@media (max-width: 576px) {
    .modal .form_search {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
}

@media (min-width: 768px) {
    .modal .form_search {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
}

@media (min-width: 992px) {
    .modal .form_search {
        grid-template-columns: calc(70% - 13px) 30%;
    }
}

.modal > .form_search input {
    width: 100%;
    background: transparent;
    padding: 15px;
    color: var(--color-light);
    border: 1px solid var(--color-light);
    box-shadow: none;
}

::-webkit-input-placeholder {
    font-weight: 500;
    color: var(--color-gray-200);
}

:-ms-input-placeholder {
    font-weight: 500;
    color: var(--color-gray-200);
}

::-ms-input-placeholder {
    font-weight: 500;
    color: var(--color-gray-200);
}

::placeholder {
    font-weight: 500;
    color: var(--color-gray-200);
}

.modal .form_search .btn_form_search {
    width: 100%;
    background: var(--color-primary);
    color: var(--color-light);
    font-weight: 600;
    padding: 15.5px 19px;
}

.modal .error_message {
    color: var(--color-danger);
    font-weight: 700;
    margin-top: 0.8rem;
    line-height: 1.2;
}

.modal > .choices_list {
    margin-top: 38px;
}

.modal > .choices_list button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: transparent;
    color: var(--color-light);
    font-weight: 500;
    text-align: left;
    margin-bottom: 28px;
    padding: 23px 12px;
    border: 1px solid var(--color-dark);
}

.modal > .choices_list button:hover {
    border-color: var(--color-gray-200);
}

.modal > .choices_list button > span {
    color: var(--color-dark);
}

.modal > .choices_list button:hover > span {
    color: var(--color-gray-200);
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    min-height: 100vh;
}

.navbar {
    position: absolute;
    top: 42px;
    left: 15px;
    right: 15px;
    display: flex;
    justify-content: space-between;
    width: calc(95% - (2 * 15px));
    margin: 0 auto;
    z-index: 100;
}

@media (max-width: 767px) {
    .navbar {
        top: 20px;
    }
}

.navbar .btn_search {
    background: var(--color-default);
    color: var(--color-light);
    font-weight: 500;
    padding: 11px 18px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.navbar .btn:hover {
    color: var(--color-dark);
    background: var(--color-light);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.125);
}

.navbar .btn_gps {
    background: var(--color-default);
    padding: 9px 9px 6px;
    border-radius: 100%;
}

.navbar .btn_gps > .icon {
    color: var(--color-light);
}

.navbar .btn_gps:hover > .icon {
    color: var(--color-dark);
}

.today_weather {
    position: relative;
    background: var(--color-dark);
    overflow: hidden;
}

/* ====================
    IMAGES
==================== */
.today_weather .today_img {
    position: relative;
    padding-top: 41px;
}

@media (max-width: 768px) {
    .today_weather .today_img {
        padding-top: 65px;
    }
}

.today_weather .today_img > div {
    position: relative;
}

.today_weather .today_img .today_bg_cloud {
    position: relative;
    left: -35%;
    opacity: 0.1;
    width: 170%;
}

.today_weather .today_img .today_weather_img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    max-width: 150px;
}

/* ====================
    CONTENT
==================== */
.today_weather .today_content .content_temperature {
    font-weight: 500;
    font-size: 144px;
    line-height: 0.8;
    color: var(--color-light);
    text-align: center;
    margin-bottom: 23px;
}

.today_weather .today_content .content_temperature > span {
    font-weight: 300;
    font-size: 48px;
}

.today_weather .today_content .content_type {
    font-weight: 600;
    font-size: 36px;
    line-height: 42px;
    text-align: center;
    color: var(--color-gray-300);
    margin-bottom: 30px;
    margin-top: 43px;
}

@media (min-width: 768px) {
    .today_weather .today_content .content_type {
        margin-top: 87px;
        margin-bottom: 48px;
    }
}

.today_weather .today_content .content_day {
    font-weight: 500;
    font-size: 1.1rem;
    line-height: 21px;
    color: var(--color-gray-100);
    text-align: center;
    margin-bottom: 30px;
}

@media (min-width: 768px) {
    .today_weather .today_content .content_day {
        margin-bottom: 48px;
    }
}

.today_weather .today_content .content_day > span {
    margin: 0 1rem;
    line-height: 11px;
    font-size: 1.25rem;
}

.today_weather .today_content .content_localisation {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 1.1rem;
    color: var(--color-gray-100);
    text-align: center;
    padding-bottom: 3rem;
}

.today_weather .today_content .content_localisation > span:first-child {
    margin-right: 4px;
}

.footer {
    bottom: 0;
    height: 100%;
    margin-top: 95px;
}

@media (min-width: 768px) {
    .footer {
        margin-top: 113px;
    }
}

.footer p {
    font-family: var(--font-family-secondary);
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.4;
    text-align: center;
    color: var(--color-gray-300);
}

.footer p a {
    color: var(--color-gray-300);
    font-weight: 700;
}

/* ====================
    HEADER
==================== */
.descriptif_weather .weather_header {
    display: none;
}

.descriptif_weather .weather_header > Button {
    background: var(--color-light);
    width: 40px;
    height: 40px;
    border-radius: 54px;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    padding: 10px 11px;
    color: var(--color-dark);
}

.descriptif_weather .weather_header > Button:hover {
    color: var(--color-light);
    background: var(--color-dark);
}

.descriptif_weather .weather_header > Button:disabled {
    background: #585676;
    color: var(--color-light);
}

.descriptif_weather .weather_header > Button:last-child {
    margin-left: 12px;
}

@media (min-width: 768px) {
    .descriptif_weather .weather_header {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 66px;
    }
}

.progressBar {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 230px;
}

.percentage {
    display: flex;
    justify-content: space-between;
    margin-bottom: 3px;
}

.bar {
    display: block;
    height: 8px;
    width: 100%;
    background: var(--color-light);
    border-radius: 80px;
}

.bar .progress {
    width: 0;
    height: 100%;
    background: #7d7ee2;
    border-radius: inherit;
    text-align: right;
    -webkit-transition: width 1s ease-in-out;
    transition: width 1s ease-in-out;
}

.progressBar > span {
    align-self: flex-end;
    margin-top: 3px;
}

.progressBar span {
    font-weight: 700;
    font-size: 0.75rem;
    line-height: 0.8rem;
    color: var(--color-gray-300);
}

/* ====================
    HIGHTLIGHTS
==================== */
.descriptif_weather .weather_today_hightlights {
    margin-top: 51px;
}

.descriptif_weather .weather_today_hightlights > h2 {
    font-weight: 700;
    font-size: 1.5rem;
    color: var(--color-light);
    margin-bottom: 32px;
}

.descriptif_weather .weather_today_hightlights .infos {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 32px;
    gap: 32px;
}

@media (min-width: 992px) {
    .descriptif_weather .weather_today_hightlights .infos {
        grid-template-columns: repeat(2, 1fr);
    }
}

.descriptif_weather .weather_today_hightlights .infos .info_item {
    background: var(--color-dark);
    padding: 22px 15px 30px;
    text-align: center;
}

.descriptif_weather .weather_today_hightlights .infos .info_item h4 {
    font-weight: 500;
    font-size: 1rem;
    color: var(--color-light);
    margin-bottom: 24px;
}

.descriptif_weather .weather_today_hightlights .infos .info_item .data {
    font-weight: 700;
    font-size: 64px;
    line-height: 75px;
    color: var(--color-light);
}

.descriptif_weather .weather_today_hightlights .infos .info_item .data > span {
    font-size: 36px;
    font-weight: 500;
}

.descriptif_weather .weather_today_hightlights .infos .info_item .option {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 14px;
    color: var(--color-light);
    margin-top: 24px;
}

.descriptif_weather
    .weather_today_hightlights
    .infos
    .info_item
    .option
    > .icon {
    background: rgba(255, 255, 255, 0.3);
    font-size: 1.5rem;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
    border-radius: 100%;
    padding: 6px;
    margin-right: 9px;
}

/* ====================
    WEEK
==================== */
.descriptif_weather .weather_week .week_day {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: var(--color-dark);
    font-weight: 500;
    font-size: 0.95rem;
    text-align: center;
    color: var(--color-light);
    padding: 1rem 26px;
}

.descriptif_weather .weather_week .week_day h2 {
    margin-bottom: 10px;
}

.descriptif_weather .weather_week .week_day > .temperature {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

.descriptif_weather .weather_week .week_day > .temperature .min {
    color: var(--color-gray-300);
}

.descriptif_weather .weather_week .week_day > .week_img {
    width: 80%;
    margin: 0 auto;
    max-width: 90px;
}

.descriptif_weather .weather_week .week_day > .week_img.sun {
    background: url(/devchallenges--weather-app/static/media/Clear.6e4f36b6.png) no-repeat;
    background-size: 60%;
    background-position: 10% 0%;
}

.descriptif_weather .weather_week .week_day > .week_img.more {
    margin-bottom: 2.75rem;
}

.descriptif_weather .weather_week .week_day > .week_img img {
    width: 100%;
}

.descriptif_weather .weather_week {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 26px;
    gap: 26px;
    margin: 0 auto;
}

@media (max-width: 991px) {
    .descriptif_weather .weather_week {
        max-width: 400px;
    }
}

@media (min-width: 992px) {
    .descriptif_weather .weather_week {
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 26px;
        gap: 26px;
    }
}

@media (min-width: 1200px) {
    .descriptif_weather .weather_week {
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 26px;
        gap: 26px;
    }
}

/* ====================
    VARIABLES
==================== */
:root {
    --gutter: 24px;

    --color-light: #e7e7eb;
    --color-dark: #1e213a;
    --color-gray-100: #88869d;
    --color-gray-200: #616475;
    --color-gray-300: #a09fb1;
    --color-bg: #100e1d;
    --color-primary: #3c47e9;
    --color-danger: #eb6b6b;
    --color-default: #737a6e;

    --font-family-primary: 'Raleway', sans-serif;
    --font-family-secondary: 'Montserrat', sans-serif;
}

/* ====================
    APP
==================== */
.App {
    background: #100e1d;
    background: var(--color-bg);
    color: #e7e7eb;
    color: var(--color-light);
    width: 100%;
    min-height: 100vh;
    overflow-x: hidden;
}

.App.grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
}

@media (min-width: 768px) {
    .App.grid {
        grid-template-columns: 40% 60%;
    }
}

@media (min-width: 1200px) {
    .App.grid {
        grid-template-columns: 30% 70%;
    }
}

/* ====================
    CONTAINER
==================== */
.container {
    position: relative;
    width: 100%;
    padding-right: 24px;
    padding-right: var(--gutter);
    padding-left: 24px;
    padding-left: var(--gutter);
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 576px) {
    .container {
        max-width: 540px;
    }
}

@media (min-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 720px;
    }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}

.today_weather .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.descriptif_weather .container {
    padding-top: 52px;
    padding-bottom: 24px;
}

@media (max-width: 768px) {
    .today_weather,
    .today_weather .container {
        min-height: 100vh;
    }
}

@media (min-width: 768px) {
    .today_weather .container,
    .descriptif_weather .container {
        padding-top: 42px;
        max-width: 904px;
        min-height: 100vh;
    }

    .today_weather .container {
        position: fixed;
        width: 40%;
        padding-bottom: 52px;
        overflow: hidden;
    }
}

@media (min-width: 1200px) {
    .today_weather .container {
        width: 30%;
    }
}

@media (min-width: 768px) and (max-width: 1200px) and (orientation: landscape) {
    .today_weather .container {
        position: absolute;
        width: 100%;
    }
}

*,
*:before,
*:after {
    box-sizing: border-box;
    outline: none;
    padding: 0;
    margin: 0;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

button {
    cursor: pointer;
    border: none;
}

button:disabled {
    cursor: default;
}

::-webkit-input-placeholder {
    font-size: 1rem;
    line-height: 1.1875;
}

:-ms-input-placeholder {
    font-size: 1rem;
    line-height: 1.1875;
}

::-ms-input-placeholder {
    font-size: 1rem;
    line-height: 1.1875;
}

button,
::placeholder {
    font-size: 1rem;
    line-height: 1.1875;
}

div,
h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
button {
    font-family: var(--font-family-primary);
    font-style: normal;
    line-height: 1;
}

a {
    text-decoration: none;
}

p {
    font-size: 1rem;
}

.icon {
    font-size: 1.4rem;
    line-height: 0.9;
}

